@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* :root {
  --dark: rgb(75,75,75);
  --medium: rgb(190,190,190);
  --light: rgb(215,215,215);
  --accent-1: rgb(90,200,250);
} */

:root {
    --dark: #2a4d69;
    --medium: #adcbe3;
    --light: #e7eff6;
    --accent-1: rgb(255, 153, 0);
    --accent-1-light: rgb(255, 200, 118);
}

* {
    font-family: "Source Sans Pro", sans-serif;
    line-height: 150%;
}

a:link,
a:visited {
    color: #2a4d69;
    color: var(--dark);
}

a:link:active,
a:visited:active {
    color: rgb(255, 200, 118);
    color: var(--accent-1-light);
}

a:hover {
    color: rgb(255, 153, 0);
    color: var(--accent-1);
}

a:active {
    color: rgb(255, 200, 118);
    color: var(--accent-1-light);
}

.grow {
    transition: all 0.2s ease-in-out;
}

.grow:hover {
    transform: scale(1.01);
}

.grow_big {
    transition: all 0.2s ease-in-out;
}

.grow_big:hover {
    transform: scale(1.1);
}

.font-01 {
    font-family: "Libre Baskerville", serif;
}

.font-02 {
    font-family: "Source Sans Pro", sans-serif;
}

.dark {
    background-color: #2a4d69;
    background-color: var(--dark);
    color: #e7eff6;
    color: var(--light);
}

.gray {
    background-color: #adcbe3;
    background-color: var(--medium);
    color: #2a4d69;
    color: var(--dark);
}

.light {
    background-color: #e7eff6;
    background-color: var(--light);
    color: #2a4d69;
    color: var(--dark);
}

.blue-under {
    border-bottom: 2px solid rgb(255, 153, 0);
    border-bottom: 2px solid var(--accent-1);
}

h1 {
    font-size: 27px;
}

h2 {
    font-size: 22px;
}

h3 {
    font-size: 20px;
}

body {
    margin: 0;
}

body::-webkit-scrollbar {
    width: 11px;
}

body::-webkit-scrollbar-track {
    background: #e7eff6;
    background: var(--light);
}

body::-webkit-scrollbar-thumb {
    background-color: #adcbe3;
    background-color: var(--medium);
    border-radius: 5px;
    border: 3px solid #e7eff6;
    border: 3px solid var(--light);
}

.top-accent-bar {
    width: 100%;
    height: 5px;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    background-color: rgb(255, 153, 0);
    background-color: var(--accent-1);
}

header {
    box-sizing: border-box;
    width: 100%;
    min-height: 108px;
    position: fixed;
    top: 0;
    left: 0;
    padding-top: 5px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.name {
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 25px;
    font-weight: bold;
}

.job-title {
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
}

main section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Add space so that elements aren't hidden by header */
main > section {
    min-height: 100vh;
    padding: 10px;
    padding-top: 108px;
    padding-bottom: 108px;
}

main section:nth-child(2n) {
    background-color: #e7eff6;
    background-color: var(--light);
}

.about p,
.contact p {
    font-size: 18px;
    margin-top: 0;
}

.about {
    box-sizing: border-box;
}

#hero-quote {
    margin-top: 20px;
    font-size: 27px;
    line-height: 170%;
    /*font-weight: bold;*/
}

section p,
.body-text {
    width: 90%;
    max-width: 1000px;
}

.pic {
    box-sizing: border-box;
    width: 90%;
    max-width: 1000px;
}

.pic {
    border-bottom: 5px solid rgb(255, 153, 0);
    border-bottom: 5px solid var(--accent-1);
}

.faIcon {
    font-size: 30px;
}

.projects .heading {
    display: inline-block;
    border-bottom: 2px solid rgb(255, 153, 0);
    border-bottom: 2px solid var(--accent-1);
}

a.btn-link {
    text-decoration: none;
    color: #2a4d69;
    color: var(--dark);
}

a.btn-link:active {
    background-color: rgb(255, 200, 118);
    background-color: var(--accent-1-light);
    color: #2a4d69;
    color: var(--dark);
}

a.btn-demo {
    margin-right: 20px;
}

.btn {
    display: inline-block;
    width: 125px;
    text-align: center;
    font-size: 20px;
    border: 2px solid #2a4d69;
    border: 2px solid var(--dark);
}

.btn:hover {
    background-color: rgb(255, 153, 0);
    background-color: var(--accent-1);
}

.contact {
    padding: 10px;
}

.contact-links {
    width: 200px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.contact-links a {
    text-decoration: none;
}

.contact p {
    text-align: center;
}

/* Desktop view */
@media screen and (min-width: 900px) {
    h1 {
        font-size: 30px;
    }

    h2 {
        font-size: 25px;
    }

    h3 {
        font-size: 23px;
    }

    /* Add info to move main text to right panel */
    header {
        max-width: 450px;
        /* Height is 100% of viewport height */
        height: 100vh;
        position: fixed;
        justify-content: start;
    }

    .name {
        padding-top: 30px;
        padding-bottom: 10px;
        font-size: 33px;
    }

    .job-title {
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: 21px;
    }

    main {
        /* Make width take up whole screen minus header/navbar width */
        width: calc(100% - 450px);
        position: absolute;
        top: 0;
        right: 0;
    }

    .about p,
    .contact p {
        font-size: 20px;
    }

    .contact p {
        font-size: 22px;
    }
}

/* STYLE THE SCROLLBAR? */

.navbar {
    width: 100%;
    height: 50px;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.navbar-cntr {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
}

.navbar-cntr a {
    text-decoration: none;
    color: var(--light);
    line-height: normal;
}

.navbar-cntr a:hover {
    color: var(--accent-1);
}

.navbar-cntr a:active {
    color: var(--accent-1-light);
}

@media screen and (min-width: 900px) {
    .navbar {
        height: 80px;
        max-width: 450px;
    }

    .navbar-cntr {
        font-size: 22px;
    }
}

#ProjectListing {
    margin-top: 75px;
}

#ProjectListing p {
    font-size: 18px;
    margin-top: 0;
}

#ProjectListing .faIcon {
    font-size: 25px;
    margin-right: 5px;
}

#ProjectListing .proj-pic {
    margin-bottom: 10px;
    border-bottom: 5px solid var(--accent-1);
}

#ProjectListing .proj-pic,
#ProjectListing .proj-text {
    width: 100%;
}

@media screen and (min-width: 900px) {
    #ProjectListing p {
        font-size: 20px;
    }

    #ProjectListing .faIcon {
        font-size: 40px;
    }
}

